import Vue from 'vue'
import Router from 'vue-router'
import routes from './routers'
import store from '@/store'
import iView from 'iview'
import { getToken, canTurnTo } from '@/libs/util'

Vue.use(Router)
const router = new Router({
  routes,
  mode: 'history'
})
const LOGIN_PAGE_NAME = 'login'

router.beforeEach((to, from, next) => {
  iView.LoadingBar.start()
  const token = getToken()
  if (!token && to.name !== LOGIN_PAGE_NAME) {
    // 
    next({
      name: LOGIN_PAGE_NAME // 
    })
  } else if (!token && to.name === LOGIN_PAGE_NAME) {
    // 
    next() // 
  } else if (token && to.name === LOGIN_PAGE_NAME) {
    // 
    next({
      name: 'home' // home
    })
  } else {
		next() 
// 		console.log("dispatch getUserInfo",to)
//     store.dispatch('getUserInfo').then(user => {
// 			console.log("user",user)
//       // name;access['super_admin'] ['super_admin', 'admin']
//       //if (canTurnTo(to.name, user.access, routes)) next() // 
// 			if (canTurnTo(to.name, 'super_admin', routes)) next() 
//       else next({ replace: true, name: 'error_401' }) // 401
//     })
  }
})

router.afterEach(to => {
  iView.LoadingBar.finish()
  window.scrollTo(0, 0)
})

export default router
